.navbar-brand,
.navbar-nav,
.navbar-text,
.navbar-content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    min-width: 0;

    &:not(:first-child) {
        margin-left: $navbar-content-horizontal-margin;
    }
}

.navbar-text {
    color: $base-text-color-light;
}


.navbar-nav {
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
    height: $navbar-height;
    ;

    &:first-child {
        &>.nav-item {
            &:first-child {
                &>.nav-link {
                    padding-left: 0;
                }
            }
        }
    }

    &:last-child {
        &>.nav-item {
            &:last-child {
                &>.nav-link {
                    padding-right: 0;
                }
            }
        }
    }

    &>li {
        height: 100%;
        margin-bottom: 0;
        position: relative;

        a {
            cursor: pointer;
            display: inline-block;
            display: flex;
            align-items: center;
            height: 100%;
            white-space: nowrap;
            margin-left: $navbar-content-horizontal-margin;
            margin-right: $navbar-content-horizontal-margin;
            color: $text-color;
            text-decoration: none !important;
            font-weight: 500;
            font-size: 1.8rem;
        }

        &.subnav::before {
            content: "";
            position: absolute;
            bottom: 0;
            left: calc(50% - #{$navbar-content-horizontal-margin/2} - 6px);
            // background-color: var(--primary-color);
            // width: 6px;
            // height: 6px;
            // border-radius: 50%;

            border-style: solid;
            border-width: 6px 6px 0 6px;
            border-color: $border-color transparent transparent transparent;
            border-radius: 2px;
            z-index: -2;
        }

        &.active>a,
        &.show>a,
        &:hover>a {
            color: var(--primary-color);
            border-bottom: 6px solid var(--primary-color);
            padding-top: 6px;
        }

        &>ul {
            z-index: 999;
            visibility: hidden;
            opacity: 0;
            transition-property: opacity, visibility;
            transition-duration: .4s, 0s;
            transition-delay: .2s, 0s;
            position: absolute;
            top: calc(#{$navbar-bottom-height} + 14px);
            min-width: 255px;
            padding: 1rem 0;
            border-radius: 5px;
            background-color: #fff;
            border: solid 1px #e0e0e0;
            margin: 0;
            list-style-type: none;
            left: calc(-50% + 9px);
            box-shadow: $box-shadow;

            &::before {
                content: "";
                z-index: 999;
                position: absolute;
                top: -15px;
                left: 17px;
                border-style: solid;
                border-width: 0 10px 15px 10px;
                border-color: transparent transparent $border-color transparent;

            }

            &::after {
                content: "";
                z-index: 999;
                position: absolute;
                top: -14px;
                left: 17px;
                border-style: solid;
                border-width: 0 10px 15px 10px;
                border-color: transparent transparent #fff transparent;
            }

            li {
                a {
                    padding-left: 1.5rem;
                    padding-right: 1.5rem;
                    font-weight: normal;
                    font-size: 1.6rem;
                    color: var(--primary-color);
                }


                &.active>a,
                &.show>a,
                &:hover>a {
                    color: var(--primary-color);
                    // border-left: 6px solid var(--primary-color);
                    // padding-left: calc(1.5rem - 6px);
                }

            }
        }

        &:hover>ul {
            visibility: visible;
            opacity: 1;
        }
    }

    .breadcrumb {
        font-size: unset;
    }
}

.navbar-brand {
    font-size: $navbar-brand-font-size;
    font-weight: $navbar-brand-font-weight;
    color: $text-color;
    margin: 1rem 1rem 0;
    position: relative;
    width: 100%;

    &:hover {
        color: $base-text-color-light;
        text-decoration: none;
    }

    img {
        height: 100%;
        max-height: $navbar-brand-image-height;
        max-width: 100%;
        margin-right: $navbar-brand-image-margin-right;

    }

    &:last-of-type img {
        margin-left: auto;
    }

}


// .navbar-large {

.navbar-nav {
    width: 100%;
    height: $navbar-bottom-height;
    display: flex;
    padding-left: 0;
    padding-right: 0;
    margin: 0;

    li a:first-child {
        margin-left: 0;
    }
}

.navbar-bottom {
    height: $navbar-bottom-height;
    background-color: white;
    position: sticky;
    top: 0;
    z-index: 20;
}

.navbar-brand {
    img {
        margin-right: 0;
    }
}

