.navbar {

    /* Navbar action button */
    .btn-action {
        // width: 4.6rem;
        padding-left: 1rem;
        padding-right: 2rem;
        border: none;
        background-color: transparent;
        box-sizing: border-box;
        box-shadow: none;
        color: var(--primary-color);
        font-size: 2rem;
        transform: rotateY(0);

        &:hover {
            background-color: transparent;
        }

        &.active {
            transform: rotateY(180deg);
        }

        &:not(.disabled):not(:disabled).active,
        &:not(.disabled):not(:disabled):focus {
            -moz-box-shadow: none;
            -webkit-box-shadow: none;
            box-shadow: none;
        }

        &::after {
            font-family: $icon-font;
            // font-weight: 900;
            // content: '\f0c9';
            content: '\ebca';
            width: 100%;
            font-size: 1.25em;
            line-height: 1em;
            vertical-align: -0.15em;
        }
    }

    .nav-search {
        position: relative;
        align-self: baseline;
        margin-top: 1.5rem;


        &>.input-group {
            min-width: 30rem;
        }

        &>.form-control,
        &>.form-group .form-control {
            min-width: 12rem;
            background-color: #f5f5f5;
        }

    }

    /* Navbar minimum widths for inline forms (Required for IE) */
    &>.form-inline,
    &>.navbar-menu>.form-inline {
        &:not(:first-child) {
            margin-right: $navbar-content-horizontal-margin;
            margin-left: $navbar-content-horizontal-margin;
        }

        &>.input-group {
            min-width: 34rem;
        }

        &>.form-control,
        &>.form-group .form-control {
            min-width: 12rem;
        }
    }
}

.nav-search {
    position: relative;
    $suggest-link-height: 3.2rem;

    .suggestions {
        color: $text-color;
        position: absolute;
        margin: 10px auto;
        top: 100%;
        left: 0;
        height: $suggest-link-height*6;
        overflow: auto;
        bottom: -3px;
        width: 100%;
        box-sizing: border-box;
        min-width: 12rem;
        background-color: $tile-color;
        border: $base-border-width solid $border-color;
        // display: none;
        visibility: hidden;
        opacity: 0;
        z-index: 100;

        -webkit-transition: opacity .4s linear;
        transition: opacity .4s linear;

        &-sm {
            height: $suggest-link-height;
        }

        &.show {
            // display: block;
            visibility: visible;
            opacity: 1;
        }

        &.fade {
            opacity: 0;
        }

        .title {
            width: 100%;
            cursor: default;
            background-color: #fcfcfc;
            padding: .5rem 1rem;
            height: 3.2rem;
            box-sizing: border-box;
            font-weight: bold;
            border-bottom: 1px solid #ccc;
            font-size: inherit;
        }

        a {
            width: 100%;
            display: block;
            padding: .5rem 1rem;
            box-sizing: border-box;
            color: $muted-color;

            // line-height: $suggest-link-height;
            &:hover {
                text-decoration: none;
                background-color: $body-color;
            }
        }

    }

    &:focus-within .suggestions.on-focus {
        // display: block;
        visibility: visible;
        opacity: 1;
    }
}

@include respond-below(md) {
    .navbar .nav-search>.input-group {
        min-width: 26rem;
    }
}


@include respond-below(sm) {

    .navbar .btn-action.active {
        transform: rotateY(0);

    }

    .navbar .nav-search>.input-group {
        min-width: 19rem;
        max-width: 100%;
    }

    .navbar-nav {
        visibility: hidden;
    }

}

