@keyframes rotate {
    0% {
        -webkit-transform: rotate(0deg) scale(0.8);
        -moz-transform: rotate(0deg) scale(0.8);
    }

    50% {
        -webkit-transform: rotate(180deg) scale(1.2);
        -moz-transform: rotate(180deg) scale(1.2);
    }

    100% {
        -webkit-transform: rotate(360deg) scale(0.8);
        -moz-transform: rotate(360deg) scale(0.8);
    }
}

@keyframes ball1 {
    0% {
        box-shadow: 3rem 0 0 var(--color-logo-gray);
    }

    50% {
        box-shadow: 0 0 0 var(--color-logo-gray);
        margin-bottom: 0;
        -webkit-transform: translate(15px, 15px);
        -moz-transform: translate(15px, 15px);
    }

    100% {
        box-shadow: 3rem 0 0 var(--color-logo-gray);
        margin-bottom: 1rem;
    }
}

@keyframes ball2 {
    0% {
        box-shadow: 3rem 0 0 var(--color-logo-blue);
    }

    50% {
        box-shadow: 0 0 0 var(--color-logo-blue);
        margin-top: -2rem;
        -webkit-transform: translate(15px, 15px);
        -moz-transform: translate(15px, 15px);
    }

    100% {
        box-shadow: 3rem 0 0 var(--color-logo-blue);
        margin-top: 0;
    }
}


.loader {
    display: none;
    position: absolute;

    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    top: 0;
    background-color: rgba(0, 0, 0, 0.1);
    z-index: 100;

    span {
        position: fixed;
        top: 50%;
        left: 50%;
        transform: -webkit-translate(-50%, -50%);
        transform: -moz-translate(-50%, -50%);
        transform: -ms-translate(-50%, -50%);
        transform: translate(-50%, -50%);
        animation: rotate 1s infinite;
        height: 5rem;
        width: 5rem;

        &:before,
        &:after {
            border-radius: 50%;
            content: '';
            display: block;
            height: 2rem;
            width: 2rem;
        }

        &:before {
            animation: ball1 1s infinite;
            background-color: var(--color-logo-green);
            box-shadow: 3rem 0 0 var(--color-logo-gray);
            margin-bottom: 1rem;
        }

        &:after {
            animation: ball2 1s infinite;
            background-color: var(--color-logo-red);
            box-shadow: 3rem 0 0 var(--color-logo-blue);
        }

    }



    &.show {
        display: block;
    }
}

@keyframes loader-spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}


@keyframes dotRect {
    0% {
        transform: rotate(0deg);
    }

    25% {
        transform: rotate(90deg);
    }

    50% {
        transform: rotate(180deg);
    }

    75% {
        transform: rotate(270deg);
    }

    100% {
        transform: rotate(360deg);
    }
}