/*
-------------------------------------------------------------------------------
Alerts
-------------------------------------------------------------------------------
*/

.alert {
    position: relative;
    padding: 1rem 2rem;
    color: $text-color;
    background-color: $tile-color;
    border: $base-border-width solid $border-color;
    border-radius: $border-radius;

    .close {
        position: absolute;
        top: 0;
        right: 0;
        cursor: pointer;
        color: inherit;
        padding: 0.4rem 0.6rem;
        line-height: 1;
        font-size: 1.6rem;
        background-color: transparent;
        border-color: transparent;

        &:focus {
            outline: 2px solid rgba(0, 0, 0, 0.5);
            outline-offset: $button-outline-offset;
        }
    }

    &.dispose {
        display: none;
    }

    &.filled {
        color: white;
    }

    @each $name in $colors {
        &.#{$name} {
            background-color: var(--#{$name}-color-very-light);
            border-color: var(--#{$name}-color);

            &.filled {
                background-color: var(--#{$name}-color);
            }
        }
    }

    a {
        cursor: pointer;
        color: inherit;
        text-decoration: underline;

        &:hover {
            color: inherit;
            opacity: 0.75;
        }
    }

    .title {
        color: inherit;
        font-size: 1.6rem;
        font-weight: $content-title-font-weight;
        margin: 0 0 0.5rem 0;
    }
}

.sticky-alerts {
    .alert {
        display: none;
        width: 27rem;
        margin-top: 1rem;
        right: -50rem;
        -moz-box-shadow: $sticky-alert-box-shadow;
        -webkit-box-shadow: $sticky-alert-box-shadow;
        box-shadow: $sticky-alert-box-shadow;

        &.show {
            right: 0;
            -webkit-transition: right .4s cubic-bezier(.25, .8, .25, 1);
            transition: right .4s cubic-bezier(.25, .8, .25, 1);
        }

        &.fade {
            visibility: hidden;
            opacity: 0;
            -webkit-transition: visibility 0s .4s, opacity .4s linear;
            transition: visibility 0s .4s, opacity .4s linear;
        }

        &.block {
            display: block;

            &.dispose {
                display: none;
            }
        }
    }
}