/*
-------------------------------------------------------------------------------
Badges
-------------------------------------------------------------------------------
*/


.badge {
    display: inline-block;
    padding: .2rem .8rem;
    background: $gray-color-light;
    border-radius: .5rem;

    a {
        color: inherit;
    }

    @each $name in $colors {
        &.#{$name} {
            color: var(--#{$name}-color-dark);
            background-color: var(--#{$name}-color-transparent);

            &.filled {
                color: white;
                background-color: var(--#{$name}-color);
            }
        }
    }

    &.pill {
        border-radius: 3rem;
        padding: .2rem 1rem;
    }
}

/* Badge group */

.badge-group {
    display: -ms-inline-flexbox;
    display: inline-flex;
}

.badge-group>.badge:not(:first-child) {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}

.badge-group>.badge:not(:last-child) {
    border-right: none;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}


a.badge,
a.badge-group {
    color: inherit; 
}
a.badge:hover,
a.badge-group:hover {
    text-decoration: none;
    background-color: $gray-color;
}

@each $name in $colors {

    a.badge.#{$name}:hover,
    a.badge-group:hover>.badge.#{$name} {
        color: var(--#{$name}-color-dark);
        background-color: var(--#{$name}-box-shadow-color);
    }
}
