/* Containers */

.container,
.container-sm,
.container-md,
.container-lg,
.container-xl,
.container-fluid {
    margin-left: auto;
    margin-right: auto;
    padding: 1rem;
}

.container-fluid {
    width: 100%;
}

.container,
.container-sm,
.container-md,
.container-lg,
.container-xl {
    width: 100%;
}

@include respond-above(sm) {

    .container,
    .container-sm {
        max-width: 72rem;
    }
}

@include respond-above(md) {

    .container,
    .container-sm,
    .container-md {
        max-width: 96rem;
    }
}

@include respond-above(lg) {

    .container,
    .container-sm,
    .container-md,
    .container-lg {
        max-width: 118rem;
    }
}

