/* Columns */

.row {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
}

.divider {
    width: 1px;
    background-color: $divider-color;
}

.hero-box .divider {
    margin: -.5rem 2rem;
}

.text-divider {
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    margin: 0rem;
    padding: 0em;
    width: auto;
    height: 50%;
    line-height: 0em;
    text-align: center;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    font-size: 1rem;

    &::before,
    &::after {
        position: absolute;
        left: 50%;
        content: '';
        z-index: 3;
        border-left: 1px solid $divider-color;
        // border-right: 1px solid rgba(255, 255, 255, 0.1);
        width: 0%;
        height: calc(100% - 1rem);
    }
    &::after {
        top: auto;
        bottom: 0px;
    }

    &::before {
        top: -100%;
    }
}

