/* Rows with equal spacing */

.row.row-eq-spacing {
    margin-top: $content-and-tile-spacing;
    margin-bottom: $content-and-tile-spacing;
    padding-left: $content-and-tile-spacing-half;
    padding-right: $content-and-tile-spacing-half;
}

.row.row-eq-spacing>[class^="col"] {
    padding-left: $content-and-tile-spacing-half;
    padding-right: $content-and-tile-spacing-half;
}

.row.row-eq-spacing>[class^="col"]>.content,
.row.row-eq-spacing>[class^="col"]>.tile {
    margin-left: 0;
    margin-right: 0;
}

.row.row-eq-spacing>[class^="col"]>.content:first-child,
.row.row-eq-spacing>[class^="col"]>.tile:first-child {
    margin-top: 0;
}

.row.row-eq-spacing>[class^="col"]>.content:last-child,
.row.row-eq-spacing>[class^="col"]>.tile:last-child {
    margin-bottom: 0;
}

@include respond-below(sm) {
    .row.row-eq-spacing {
        margin-top: $content-and-tile-spacing-xs;
        margin-bottom: $content-and-tile-spacing-xs;
