/*
-------------------------------------------------------------------------------
Sidebar content
-------------------------------------------------------------------------------
*/

.sidebar-menu {

	// blank space at the bottom shows user that there is nothing more to scroll
	padding-bottom: 4rem;

	.content {
		display: block;
		padding: 0;
		margin: $sidebar-menu-vertical-margin $sidebar-item-horizontal-spacing;
	}

	.content>ul,
	.content>ol {
		margin-left: 0;
	}

	.divider {
		padding: 0;
		margin: $sidebar-item-vertical-spacing $sidebar-item-horizontal-spacing;
		height: 1.5px;
		background-color: $border-color;
	}

	a {
		display: block;
		padding: $sidebar-item-vertical-spacing $sidebar-item-horizontal-spacing;
		margin: 0;
		min-height: 3rem;
		height: auto;
		color: $base-text-color-light;
		background-color: transparent;
		border: 0 solid transparent;
		border-radius: 0;
		text-decoration: none;


		&:hover {
			color: $text-color;
			background-color: transparent;
			border-color: transparent-hover;
			text-decoration: none;
		}

		&.active {
			background-color: transparent;
			border-color: transparent;
			font-weight: 500;
			// &:hover {
			// 	background-color: transparent-hover;
			// 	border-color: transparent-hover;
			// }
		}


		&.indent {
			padding-left: 4rem;
			position: relative;

			&::before {
				content: '';
				position: absolute;
				left: 2.5rem;
				width: 8px;
				height: 8px;
				border-radius: 50%;
				background-color: #efefef;
				// box-shadow: $box-shadow;
				top: calc(50% - 4px);
				-webkit-transition: background-color .5s linear;
				-ms-transition: background-color .5s linear;
				transition: background-color .5s linear;

			}

			&.active::before,
			&:hover::before {
				background-color: var(--primary-color);
			}
		}

		&:not(.with-icon):not(.indent) {
			border-left: 0px solid transparent;
			padding-left: 2.5rem;

			&.active {
				border-left-width: 8px;
				padding-left: calc(2.5rem - 8px);
				border-color: var(--primary-color);
			}

			@each $name in $colors {
				&.#{$name} {
					border-color: var(--#{$name}-color);
				}
			}
		}

		&.with-icon {
			display: -ms-flexbox;
			display: flex;
			-ms-flex-align: center;
			align-items: center;
			padding: $sidebar-item-vertical-spacing $sidebar-item-horizontal-spacing;

			&>.icon,
			&>i {
				display: -ms-flexbox;
				display: flex;
				-ms-flex-align: center;
				align-items: center;
				-ms-flex-pack: center;
				justify-content: center;
				width: $sidebar-icon-width-height;
				height: $sidebar-icon-width-height;
				font-size: 2rem;
				margin-right: 1rem;
				color: $base-text-color-light;
				background-color: rgba(0, 0, 0, 0.05);
				border: 2px solid transparent;
				border-radius: 50%;
			}

			&:hover {

				&>.icon,
				&>i {
					border-color: $text-color;
				}
			}

			&.active {

				.icon,
				i {
					background-color: $text-color;
					border-color: transparent;
				}
			}

			@each $name in $colors {
				&.#{$name} {

					.icon,
					i {
						color: var(--#{$name}-color);
					}

					&.active {

						.icon,
						i {
							background-color: var(--#{$name}-color);
						}
					}

					&:hover {

						.icon,
						i {
							border-color: var(--#{$name}-color);
						}
					}
				}
			}

			&.active {

				.icon,
				i {
					color: white;
				}

			}
		}
	}

	.title {
		text-transform: uppercase;
		padding: 0;
		margin: 2.5rem 2.5rem 0.5rem;
		font-size: 1.4rem;
		font-weight: normal;
		color: $border-color;
		border-bottom: 1.5px solid $border-color;
	}

	.brand {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-align: center;
		align-items: center;
		padding: 0;
		margin: $sidebar-item-vertical-spacing $sidebar-item-horizontal-spacing;
		font-size: $navbar-brand-font-size;
		font-weight: $navbar-brand-font-weight;
		color: $text-color;

		&:hover {
			color: $base-text-color-light;
			text-decoration: none;
		}

		img {
			// height: $navbar-brand-image-height;
			margin-right: $navbar-brand-image-margin-right;
			width: 100%;
		}
	}

}

.mobile-nav {
	display: none;
	margin-bottom: 1.5rem;
	padding-bottom: 1.5rem;
	border-bottom: 1px solid $border-color;	
}

@include respond-below(sm) {
	.mobile-nav {
		display: block;
	}
}

.spacer {
	height: 2rem;
}


.sidebar-topnav {
	position: absolute;
	top: 1rem;
	left: 0;
	text-align: center;
	width: 100%;

	ul {
		padding: 0;
		list-style-type: none;
		white-space: nowrap;
		overflow: hidden;

		li {
			display: inline-block;
			padding: 0 16px;
			border-left: 1px solid #cbcbcb;
			text-align: center;

			&:first-child {
				border-left: none;
			}

			a {
				display: block;
				color: #cbcbcb;
				text-transform: uppercase;
				font-size: 12px;
				font-size: 1.0rem;

				&:hover {
					color: var(--primary-color);
					text-decoration: none;
				}
			}

			i {
				display: block;
				padding-top: 3px;
				margin-bottom: 9px;
				font-size: 24px;
				font-size: 2rem;
			}
		}
	}
}