/*
-------------------------------------------------------------------------------
Tables
-------------------------------------------------------------------------------
*/


.table {
	width: 100%;
	// margin: 2rem 0;
	border-collapse: collapse;
	border: 1px solid $border-color;
	-webkit-box-shadow: $box-shadow;
	-moz-box-shadow: $box-shadow;
	box-shadow: $box-shadow;
	background-color: $tile-color;

	thead {
		tr {
			background-color: $table-head-color;
			border-bottom: $base-border-width solid $border-color;
		}
	}

	tfoot {
		tr {
			background-color: $table-head-color;
			border-top: $base-border-width solid $border-color;
		}
	}

	tbody {
		tr {
			&:not(:last-child) {
				border-bottom: $base-border-width solid $border-color;
			}

			.ref {
				text-align: end;

				img {
					max-height: 3rem;
					max-width: 5rem;
					margin: -1rem 0;
				}
			}
		}
	}

	th {
		font-weight: 500;
	}

	th,
	td {
		text-align: left;
		padding: 1rem 1.4rem;

	}

	&.simple {
		border: none;
		-webkit-box-shadow: none;
		-moz-box-shadow: none;
		box-shadow: none;
		margin: none;
		background-color: transparent;

		thead {
			tr {
				background-color: transparent;
			}
		}

		tfoot {
			tr {
				background-color: transparent;
			}
		}
	}

	&.separate {
		border-collapse: separate;
		border-spacing: 0.5rem;
		background-color: transparent;
		border: none;
		-webkit-box-shadow: none;
		-moz-box-shadow: none;
		box-shadow: none;
		margin: -0.5rem;

		&.wide {
			border-spacing: 1rem;
		}

		td,
		th {

			border: 1px solid $border-color;
			-webkit-box-shadow: $box-shadow;
			-moz-box-shadow: $box-shadow;
			box-shadow: $box-shadow;
		}

		tbody {

			td,
			th {
				background-color: $tile-color;
			}
		}
	}

	&.with-label {
		margin-top: 0;
	}


	&.sm {

		th,
		td {
			padding: 0.6rem 1rem;
		}
	}

	&.md {

		th,
		td {
			padding: 1.2rem 1.5rem;
		}
	}

	&.lg {

		th,
		td {
			padding: 1.4rem 1.8rem;
		}
	}

	&.hover {
		tbody tr:hover {

			th,
			td {
				background-color: rgba(0, 0, 0, 0.05);
			}
		}
	}

	&.striped {
		tbody tr:nth-child(even) {
			background-color: $body-color;
		}
	}

	// colored rows
	tbody {

		@each $name in $colors {
			tr.#{$name} {
				td,
				th {

					@if $name==muted {
						color: var(--muted-color);
					}
					@else {
						background-color: var(--#{$name}-color-very-light);
					}
				}
			}
		}
	}
}


.table-responsive {
	overflow-x: auto;
	overflow-y: hidden;
	// margin-bottom: 2rem;

	.table {

		td,
		th {
			white-space: nowrap;
		}

		// margin-bottom: 0;
	}
}


.table-label {
	border-left: 1px solid $border-color;
	margin: 2rem 0 0;
	padding: 0rem 2rem 1rem 1rem;
	font-weight: 500;
	font-style: italic;
	display: block;
}