a {
	color: $link-color;
	text-decoration: none;
	cursor: pointer;

	&:hover {
		color: $link-color-hover;
		text-decoration: underline;
	}

	&.link {
		&:hover {
			// color: var(--primary-color-light);
			text-decoration: none;
		}

		&::after {
			font-family: $icon-font;
			// font-weight: 900;
			color: $link-color;
			// content: '\f061';
			content: '\e95c';
			margin-left: .5rem;
			line-height: inherit;
			font-size: 1.3em;
			vertical-align: -0.2em;
		}

		&[target="_blank"]::after {
			content: "\e97f";
		}
	}

	&.colorless {
		color: inherit;
	}
}

.hyperlink-underline {
	text-decoration: underline;
}

$link-list-px : 1.5rem;

.link-list {
	a {
		position: relative;
		display: block;
		padding: $link-list-px 5.5rem $link-list-px 2.2rem;
		border-radius: $border-radius;
		background-color: #fff;
		color: $text-color;
		font-size: 1.8rem;
		margin: 1rem 0;
		box-shadow: $box-shadow;

		&:hover {
			text-decoration: none;
		}

		&::after {
			position: absolute;
			top: 0;
			right: 0;
			// content: '\f061';
			content: '\e95c';
			// font-weight: 900;
			font-family: $icon-font;
			margin: 0 0 0 1rem;
			padding: $link-list-px 2rem;
			color: #fff;
			height: 100%;
			background-color: var(--primary-color);

			border-top-right-radius: $border-radius;
			border-bottom-right-radius: $border-radius;
		}
	}

	&-sm {
		$link-list-px: $link-list-px - .5rem;

		a {
			padding: $link-list-px 5.5rem $link-list-px 2.2rem;
			font-size: 1.4rem;

			&::after {
				padding: $link-list-px $link-list-px + .5rem;
			}
		}
	}
}