/* Halfmoon Highlight JS theme */

.hljs {
    display: block;
    overflow-x: auto;
    padding: 3rem;
    color: #383a42;
    background-color: rgba(0, 0, 0, 0.025);
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
  }
  @media (max-width: 540px) {
    .hljs {
      padding: 3rem 2rem 2rem 2rem;
    }
  }
  .hljs-comment,
  .hljs-quote {
    color: #a0a1a7;
  }
  .hljs-doctag,
  .hljs-keyword,
  .hljs-formula {
    color: #a626a4;
  }
  .hljs-section,
  .hljs-name,
  .hljs-selector-tag,
  .hljs-deletion,
  .hljs-subst {
    color: var(--danger-color);
  }
  .hljs-literal {
    color: var(--primary-color);
  }
  .hljs-string,
  .hljs-regexp,
  .hljs-addition,
  .hljs-attribute,
  .hljs-meta-string {
    color: var(--success-color);
  }
  .hljs-built_in,
  .hljs-class .hljs-title {
    color: var(--primary-color-dark);
  }
  .hljs-attr,
  .hljs-variable,
  .hljs-template-variable,
  .hljs-type,
  .hljs-selector-class,
  .hljs-selector-attr,
  .hljs-selector-pseudo,
  .hljs-number {
    color: var(--primary-color-light);
  }
  .hljs-symbol,
  .hljs-bullet,
  .hljs-link,
  .hljs-meta,
  .hljs-selector-id,
  .hljs-title {
    color: var(--primary-color);
  }
  .hljs-emphasis {
    font-style: italic;
  }
  .hljs-strong {
    font-weight: bold;
  }
  .hljs-link {
    text-decoration: underline;
  }
  