/*
-------------------------------------------------------------------------------
SASS variables
-------------------------------------------------------------------------------
*/


/* Base variables */

$font-family: 'Rubik',
        'Roboto',
        Helvetica,
        Arial,
        sans-serif;
$icon-font: 'Phosphor';

$base-html-font-size: 62.5%;
/* 62.5% is used for easier calculations, because the base is now set to 10px, so 1.4rem = 14px for example. */
$base-html-font-size-1600: 70%;
/* For screens with widths >= 1600px */
$base-html-font-size-1920: 75%;
/* For screens with widths >= 1920px */
$base-html-font-size-2300 : 83%;

$max-page-width : 150rem;
// 160rem;

$base-font-size: 1.4rem;
$base-line-height: 1.5;
$base-border-width: 1px;
$border-radius: 2pt;
//0.4rem

$box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.15);
$box-shadow-long: 0px 3px 3px 0px rgba(0, 0, 0, 0.3);


/* COLORS */


// DSMZ colors

// neutral
$color-logo-red : #B61F29;
$color-logo-blue : #006EB7;
$color-logo-green : #1FA138;
$color-gray-80: #575756;
$color-gray-60: #878787;
$color-gray-40: #b2b2b2;
$color-gray-20: #dadada;

// warm
$color-warm-darkred : #5F272A;
$color-warm-orange : #EE7203;
$color-warm-red : #DD0B2F;
$color-warm-yellow : #ECAF00;

// cold
$color-cold-darkblue : #13357A;
$color-cold-lightblue : #83D0F5;
$color-cold-blue : #4C96CB;
$color-cold-green : #95BB20;
$color-cold-purple : #9a499c;


/* Primary colors */

$primary-color: $color-logo-blue;
$primary-color-light: mix(white, $primary-color, 8%);
$primary-color-very-light: mix(white, $primary-color, 80%);
$primary-color-dark: mix(black, $primary-color, 8%);
$primary-color-very-dark: mix(black, $primary-color, 80%);
$primary-color-transparent: rgba($primary-color, 0.2);
$primary-box-shadow-color: rgba($primary-color, 0.3);
$primary-box-shadow-color-darker: rgba($primary-color, 0.6);

/* Success colors */

$success-color: $color-logo-green;
$success-color-light: mix(white, $success-color, 8%);
$success-color-very-light: mix(white, $success-color, 80%);
$success-color-dark: mix(black, $success-color, 8%);
$success-color-very-dark: mix(black, $success-color, 80%);
$success-color-transparent: rgba($success-color, 0.2);
$success-box-shadow-color: rgba($success-color, 0.3);
$success-box-shadow-color-darker: rgba($success-color, 0.6);

/* Muted colors */


$muted-color: $color-gray-60;
$muted-color-light: mix(white, $muted-color, 8%);
$muted-color-very-light: mix(white, $muted-color, 80%);
$muted-color-dark: mix(black, $muted-color, 8%);
$muted-color-very-dark: mix(black, $muted-color, 80%);
$muted-color-transparent: rgba($muted-color, 0.2);
$muted-box-shadow-color: rgba($muted-color, 0.3);
$muted-box-shadow-color-darker: rgba($muted-color, 0.6);

/* Danger colors */

$danger-color: $color-logo-red;
$danger-color-light: mix(white, $danger-color, 8%);
$danger-color-very-light: mix(white, $danger-color, 80%);
$danger-color-dark: mix(black, $danger-color, 8%);
$danger-color-very-dark: mix(black, $danger-color, 80%);
$danger-color-transparent: rgba($danger-color, 0.2);
$danger-box-shadow-color: rgba($danger-color, 0.3);
$danger-box-shadow-color-darker: rgba($danger-color, 0.6);



/* Signal colors */

$signal-color: $color-warm-yellow;
$signal-color-light: mix(white, $signal-color, 8%);
$signal-color-very-light: mix(white, $signal-color, 80%);
$signal-color-dark: mix(black, $signal-color, 8%);
$signal-color-very-dark: mix(black, $signal-color, 80%);
$signal-color-transparent: rgba($signal-color, 0.2);
$signal-box-shadow-color: rgba($signal-color, 0.3);
$signal-box-shadow-color-darker: rgba($signal-color, 0.6);



// other colors
$dark-color: #191c20;
$dark-color-light: #25282c;

/* Gray colors */
$gray-color: #e7e8e9;
$gray-color-light: #f1f1f1;
$gray-color-very-light: #f7f7f7;

$body-color: $gray-color-light;
$text-color: #464646;
$border-color: #afafaf;
$divider-color: rgba($border-color, 0.5);

$base-text-color-light: rgba($text-color, 0.8);
$dm-base-text-color: rgba(255, 255, 255, 0.8);

$muted-text-color: #b3b3b3;
$tile-color: white;
$table-head-color: #dfdfdf;
$link-color : $color-logo-blue;
$link-color-hover : mix(white, $link-color, 8%);

/* Navbar */
$navbar-height: 9rem;
$navbar-bottom-height : 6rem;
$navbar-large-height: $navbar-height+$navbar-bottom-height;

$navbar-horizontal-padding: 1rem;
$navbar-border-color: $border-color;
$navbar-box-shadow: none;
$navbar-fixed-bottom-box-shadow: none;

/* Sidebar */

$sidebar-width: 25rem;
$sidebar-bg-color: white;
$sidebar-box-shadow: 1px 0px 1px 0px rgba(0, 0, 0, 0.15);

$sidebar-overlay-bg-color: rgba(0, 0, 0, 0.75);
$sidebar-menu-vertical-margin: 2.5rem;
$sidebar-item-horizontal-spacing: 2.5rem;
$sidebar-item-vertical-spacing: 0.5rem;
$sidebar-icon-width-height: 3rem;


/* Box shadows */

$shadow-color: rgba(0, 0, 0, 0.21);
$dm-shadow-color: rgba(0, 0, 0, 0.4);
$small-shadow: 0 0.1rem 0.2rem $shadow-color;
$shadow: 0 0.2rem 0.4rem $shadow-color;
$large-shadow: 0 0.4rem 0.8rem $shadow-color;


/* Content and tile */

$content-and-tile-spacing: 2rem;
$content-and-tile-spacing-xs: 1rem;
$content-and-tile-spacing-half: calc(#{$content-and-tile-spacing}/2);
$content-and-tile-spacing-xs-half: calc(#{$content-and-tile-spacing-xs}/2);
$content-title-font-size: 2rem;
$content-title-font-weight: 500;

$button-bg-color: #f5f5f5;
$button-border-color: #c8c8c8;
$button-box-shadow: 0 0.2rem 0 rgba(0, 0, 0, 0.05);
$button-box-shadow-focus: 0 0 0 0.2rem $primary-box-shadow-color-darker;
$button-outline-color-focus: transparent;


$dropdown-arrow-size: 0.8rem;
$dropdown-arrow-size-negative: calc(#{$dropdown-arrow-size} * -1);
$dropdown-arrow-inner-spacing: calc(0.2rem + (#{$dropdown-arrow-size} * -1));
$dropdown-arrow-horizontal-spacing: 0.8rem;
$dropdown-content-padding: 1.2rem;
$dropdown-item-padding: 0.6rem $dropdown-content-padding;

// Alerts

$sticky-alert-box-shadow: 0 0.2rem 0 rgba(0, 0, 0, 0.045);


$tooltip-arrow-width: 1rem;

$progress-group-item-margin: 0.4rem;


$breakpoints: (
        xs: 576px,
        sm: 768px,
        md: 992px,
        lg: 1200px,
        xl: 1600px
);

$colors: (
        primary,
        success,
        muted,
        danger,
        signal
);


