/*
-------------------------------------------------------------------------------
18. Breadcrumb [hm-18]
-------------------------------------------------------------------------------
*/
.breadcrumb {
    padding: 0;
    // background-color: transparent;
    border-radius: 0;
    text-transform: uppercase;
    font-size: smaller;
    button {
    border: 0 solid transparent;

    }
    li, button {
        display: inline-block;
        list-style: none;
        margin-bottom: 0;
        &:before {
            font-family: $icon-font;
			// font-weight: 900;
			color: $muted-color;
			// content: '\f054';
            content: '\ea00';
            font-size: 1.25em;
            line-height: 1em;
            vertical-align: -0.15em;
            margin-left: 1rem;
            margin-right: 1.5rem;
        }
        &:first-child:before {
            display: none;
        }
        &.active a {
            pointer-events: none;
            color: inherit;
        }
        
    }

}
.breadcrumb-container {
    width: 100%;
    background-color: var(--primary-color-dark);
    padding: 1rem 2rem;
    .breadcrumb {
        margin-bottom: 0;
    }
    a {
        color: white;
    &.active {
        color: white;
    }
    }
}